<template>
  <div class="coupon-activity">
    <div class="m-coupons">
      <img src="https://dn-weazm-work.qbox.me/coupons/banner2.jpg" alt="" class="cover">
      <div class="panel">
        <coupon v-for="coupon in coupons" :coupon="coupon" v-show="!$loadingRouteData"></coupon>
        <loader v-show="$loadingRouteData"></loader>
        <button class="u-btn u-btn-danger" v-show="currentPage < totalPages"
                @click.prevent="loadMore" :disabled="loading">
          <span v-show="!loading">加载更多</span>
          <span v-show="loading">加载中...</span>
        </button>
      </div>
      <div class="coupon-rules" style="padding-bottom: 7rem;">
        <h3><span>使用细则</span></h3>
        <p>
          燕塘优鲜达线上商场满减券（以下简称满减券）是燕塘乳业对广大消费者的一种优惠和回馈，为使更多的消费者享受到这种优惠，特制定燕塘优鲜达线上商城优惠券规则（以下简称"本规则"）。如果您接受并使用满减券，那么您便接受了本规则。</p>
        <ul>
          <li>满减券不能兑换现金</li>
          <li>每张满减券仅能使用一次，不找零，不退换</li>
          <li>使用满减券抵扣部分的金额不开具发票</li>
          <li>满减券应在券面标示或系统显示的有效期内使用，过期作废</li>
          <li>用户同意按照满减券规则使用各类满减券</li>
          <li>按满足满减券使用条件的商品统计，满减券会按比例抵扣在每个符合条件的商品上</li>
          <li>使用满减券的订单，未支付或已支付未配货的订单如果发生订单取消在成功后，系统会在24小时内把满减券退回帐号，有效期不变</li>
          <li>使用满减券的订单为正在配货中后，订单所使用满减券将不再返回用户帐号中，该满减券将不可再使用</li>
          <li>违背优惠券使用规则的处理发生以下情形时，燕塘优鲜达线上商城有权视情况选择冻结或作废相关顾客持有的满减券、取消使用满减券的订单、取消该用户的用券资格、停止提供服务：
            <ul>
              <li>非以个人消费为目的而使用满减券的</li>
              <li>伪造、购买、销售、转让满减券，或使用伪造、购买、转让的满减券的</li>
              <li>违反本规则或优惠券中载明的规则，使用满减券的</li>
            </ul>
          </li>
          <li>本规则由燕塘优鲜达线上商城依据国家相关法律法规及规章制度予以解释</li>
        </ul>
      </div>
    </div>
  </div>
  <nav-foot></nav-foot>
</template>

<script>
  import Coupon from './coupon.vue'
  import Loader from './../../Share/loader.vue'
  import NavFoot from './../../Mall/Shared/navfoot.vue'

  export default{
    name: 'Avaliable',

    components: {
      Coupon,
      Loader,
      NavFoot
    },

    data: function () {
      return {
        getProcess: false,
        getSuccess: false,
        coupons: [],
        totalPages: 1,
        currentPage: 1,
        loading: false
      }
    },

    methods: {
      loadMore: function () {
        var self = this
        self.loading = true
        this.$http.get('/promotions/coupons?&page=' + (this.currentPage + 1)).then(
          function (data) {
            self.coupons = self.coupons.concat(data.data.data)
            self.totalPages = data.data.meta.pagination.total_pages
            self.currentPage = data.data.meta.pagination.current_page
            self.loading = false
          },
          function (data) {
            window.alert('加载失败,请重新尝试')
          }
        )
      }
    },
    route: {
      data: function () {
        return this.$http.get('/promotions/coupons').then(
          function (data) {
            return {
              coupons: data.data.data,
              totalPages: data.data.meta.pagination.total_pages,
              currentPage: data.data.meta.pagination.current_page
            }
          },
          function (data) {
            window.alert(JSON.stringify(data))
          }
        )
      }
    },
    events: {
      'getFail': function () {
        window.alert('你已经领取过了哦!')
      }
    }
  }
</script>

<style lang="scss">
  $light: #fff;
  $dark: #8c8c8c;

  .m-coupons {
    background: #98d0ff;
  }

  .coupon-rules {
    padding: 0 2rem;
    font-size: 1.2rem;
    color: #fff;

    &.dark {
      color: $dark;
      h3 {
        color: $dark;
        span {
          &:before,
          &:after {
            border-top: 1px solid $dark;
          }
        }
      }
    }

    h3 {
      position: relative;
      overflow: hidden;
      text-align: center;
      font-size: 1.4rem;
      color: #fff;
      margin-bottom: 1rem;

      span {
        display: inline-block;
        vertical-align: baseline;
        zoom: 1;
        *display: inline;
        *vertical-align: auto;
        position: relative;
        padding: 0 20px;

        &:before,
        &:after {
          content: '';
          display: block;
          width: 1000px;
          position: absolute;
          top: 0.70em;
          border-top: 1px solid white;
        }

        &:before {
          right: 100%;
        }
        &:after {
          left: 100%;
        }
      }
    }
    & > ul {
      list-style-type: decimal;
      padding-left: 2rem;
      line-height: 180%;

      li > ul {
        list-style-type: circle;
        padding-left: 2rem;
      }

    }
  }

  .m-coupons .cover {
    width: 100%;
    height: auto;
  }

  .m-coupons .panel {
    width: 90%;
    margin: 2rem auto;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 3px;
  }

  .m-coupons .title {
    font-size: 2rem;
    text-align: center;
    color: #C71A40;
  }

  .coupon-activity {
    width: 100%;
    height: 100%;
  }

  .u-btn {
    &.u-btn-danger {
      background: #c71a40;
      color: #fff;
      border-radius: 3px;
      width: 60%;
      display: block;
      margin: 2rem auto 1rem;
      &:disabled {
        background: #8c8c8c;
        color: #fff;
      }
      .iconfont {
        -webkit-font-smoothing: antialiased;
        font-size: 1.8rem;
      }
    }
  }
</style>



